import React from 'react';
import { Dropdown, Menu } from 'semantic-ui-react';
import { GetSideBarData } from '../SidebarData';

const SideBarNav = ({ user }) => {
    const data = GetSideBarData(user.isAdmin) || [];
    if (!user)
        return null;
    return (
        <Menu borderless inverted stackable widths={data.length} style={{ borderRadius: 0 }} >
            {data.map(item => <Dropdown
                simple
                item
                key={item.text}
                text={item.text}
                icon={item.icon}
                style={{ display: 'flex', justifyContent: 'space-evenly' }}
                options={item.subs.map(sub => ({ ...sub, permission: 1, active: false }))}
            />)}

        </Menu>
    );
};

export default SideBarNav;