import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { RequireAuth, RequireNoAuth } from '../middlewares/HOC';
import getPaths from './PATHS';

import {
    LandginHome,
    Signin, ResetPassword, Activation,
    AdminDashboard,

    AdminAdmins, AdminCompanies, AdminCompanyTypes, AdminRoles, AdminClients,
    AdminPackages, AdminPromotions, AdminSubscriptions,
    AdminSystemSettings, AdminAbout, AdminContacts, AdminTerms, AdminPrivacy, AdminFAQ, AdminComplaints,
    AdminCategories, AdminBrands, AdminProducts, AdminOrders, AdminOffers, AdminAds,
    AdminPayments, AdminStats, AdminUsage, AdminCategoriesReport, AdminBrandsReport, AdminCompanyTypesReport,


} from '../pages';

const Router = () => {
    const PATHS = getPaths();
    return <Switch>
        {/* Basic Routes */}
        <Route path={PATHS.HOME.URL} exact component={LandginHome} />
        <Route path={PATHS.RESET_PASSWORD.URL} exact component={ResetPassword} />
        <Route path={PATHS.ACTIVATION.URL} exact component={Activation} />

        {/* <Route path="/signup" exact component={RequireNoAuth(Signup)} /> */}
        <Route path={PATHS.SIGNIN.URL} exact component={RequireNoAuth(Signin)} />
        {/* <Route path="/profile" exact component={Profile} /> */}



        <Route path={PATHS.ADMIN_DASHBOARD.URL} exact component={RequireAuth(AdminDashboard)} />

        <Route path={PATHS.ADMIN_ADMINS.URL} exact component={RequireAuth(AdminAdmins)} />
        <Route path={PATHS.ADMIN_ROLES.URL} exact component={RequireAuth(AdminRoles)} />

        <Route path={PATHS.ADMIN_COMPANIES_TYPES.URL} exact component={RequireAuth(AdminCompanyTypes)} />
        <Route path={PATHS.ADMIN_COMPANIES.URL} exact component={RequireAuth(AdminCompanies)} />
        <Route path={PATHS.ADMIN_CLIENTS.URL} exact component={RequireAuth(AdminClients)} />


        <Route path={PATHS.ADMIN_PACKAGES.URL} exact component={RequireAuth(AdminPackages)} />
        <Route path={PATHS.ADMIN_PROMOTIONS.URL} exact component={RequireAuth(AdminPromotions)} />
        <Route path={PATHS.ADMIN_SUBSCRIPTIONS.URL} exact component={RequireAuth(AdminSubscriptions)} />


        <Route path={PATHS.ADMIN_SYSTEM_SETTINGS.URL} exact component={RequireAuth(AdminSystemSettings)} />
        <Route path={PATHS.ADMIN_ABOUT.URL} exact component={RequireAuth(AdminAbout)} />
        <Route path={PATHS.ADMIN_CONTACTS.URL} exact component={RequireAuth(AdminContacts)} />
        <Route path={PATHS.ADMIN_TERMS.URL} exact component={RequireAuth(AdminTerms)} />
        <Route path={PATHS.ADMIN_PRIVACY.URL} exact component={RequireAuth(AdminPrivacy)} />
        <Route path={PATHS.ADMIN_FAQ.URL} exact component={RequireAuth(AdminFAQ)} />
        <Route path={PATHS.ADMIN_COMPLAINTS.URL} exact component={RequireAuth(AdminComplaints)} />


        <Route path={PATHS.ADMIN_CATEGORIES.URL} exact component={RequireAuth(AdminCategories)} />
        <Route path={PATHS.ADMIN_BRANDS.URL} exact component={RequireAuth(AdminBrands)} />
        <Route path={PATHS.ADMIN_PRODUCTS.URL} exact component={RequireAuth(AdminProducts)} />
        <Route path={PATHS.ADMIN_ORDERS.URL} exact component={RequireAuth(AdminOrders)} />
        <Route path={PATHS.ADMIN_OFFERS.URL} exact component={RequireAuth(AdminOffers)} />
        <Route path={PATHS.ADMIN_ADS.URL} exact component={RequireAuth(AdminAds)} />


        <Route path={PATHS.ADMIN_PAYMENTS.URL} exact component={RequireAuth(AdminPayments)} />
        <Route path={PATHS.ADMIN_STATS.URL} exact component={RequireAuth(AdminStats)} />
        <Route path={PATHS.ADMIN_USAGE.URL} exact component={RequireAuth(AdminUsage)} />
        <Route path={PATHS.ADMIN_CATEGORIES_REPORT.URL} exact component={RequireAuth(AdminCategoriesReport)} />
        <Route path={PATHS.ADMIN_BRANDS_REPORT.URL} exact component={RequireAuth(AdminBrandsReport)} />
        <Route path={PATHS.ADMIN_COMPANY_TYPES_REPORT.URL} exact component={RequireAuth(AdminCompanyTypesReport)} />


        {/* {renderRoutes(user.roleId)} */}

        {/* Invalid Routes */}
        <Route>
            <Route path="*" exact component={LandginHome} />
            <Redirect from='*' to='/' />
        </Route>

    </Switch>;
};

export default Router;
